import React from 'react'

const NotFoundPage = () => (
  <div style={{ display: 'flex', width: '100%', height: '100vh', justifyContent: 'center', alignItems: 'center', flexDirection: 'column' }}>
    <h1 style="font-size:5rem;">:-(</h1>
    <h2 style="font-size:2rem;">404&mdash; That's an error.</h2>
    <h4 style="max-width:55ch;">The page you're looking for was not found on this server. That's all we know.</h4>
    <h5>ERR::PAGE_NOT_FOUND</h5>
  </div>
)

export default NotFoundPage



